import {Link} from 'gatsby';
import {StaticImage} from 'gatsby-plugin-image';
import React from 'react';

import Button from './common/Button';
import {RoundArrow} from './common/icons';

const HeaderItem = ({name, link, active}) => (
  <Link to={link} className={`flex items-center ${active ? 'text-n-100' : ''}`}>
    {active && <div className="bg-primary h-2 w-2 rounded-full mr-4" />}
    {name}
  </Link>
);

export default () => (
  <header className="container bg-n-500 py-6 px-4 sm:px-0 fixed z-50 inset-x-0">
    <div className="flex justify-between items-center px-10">
      <StaticImage src="../images/logo.png" alt="Elite Alliance" height={17} />
      <div className="flex text-n-400 font-mono space-x-12">
        <HeaderItem name="About Us" link="/" active />
        <HeaderItem name="Contact" link="/" />
        <Button kind="primary" icon={<RoundArrow />}>
          Sign In
        </Button>
      </div>
    </div>
  </header>
);
