import React from 'react';

const config = {
  primary: 'bg-n-400 text-n-500',
  secondary: 'bg-n-500 text-n-200',
};

const Button = ({
  kind = 'primary',
  className,
  children,
  type,
  icon,
  ...props
}) => (
  <button
    // eslint-disable-next-line react/button-has-type
    type={type || 'button'}
    className={`flex items-center font-mono uppercase h-10 text-sm leading-3 whitespace-nowrap focus:ring-4 rounded-full font-bold ${
      config[kind]
    } ${icon ? 'pl-10 pr-5' : 'px-8'} ${className || ''}`}
    {...props}
  >
    {children}
    {icon && <div className="flex items-center text-2xl ml-6">{icon}</div>}
  </button>
);

export default Button;
